<template>
  <div>
    <p></p>

    <!--  -->
    <div>
      <input
        id="creditcard"
        type="checkbox"
        v-model="creditcard"
      >
      <label for="creditcard"></label>
      <select v-model="creditcardCredit">
        <option disabled value=""></option>
        <option>0.5%</option>
        <option>1%</option>
      </select>
    </div>

    <!-- PayPay -->
    <div>
      <input
        id="paypayMoney"
        type="checkbox"
        v-model="paypayMoney"
      >
      <label for="paypayMoney">PayPay()</label>
    </div>

    <!-- visa Line Pay Card -->
    <div>
      <input
        id="visaLinePay"
        type="checkbox"
        v-model="visaLinePay"
      >
      <label for="visaLinePay">visa LINE Pay</label>
    </div>

    <!-- Line Pay -->
    <div>
      <input
        id="linePay"
        type="checkbox"
        v-model="linePay"
      >
      <label for="linePay">LINE Pay</label>
      <select v-model="lineRank">
        <option disabled value=""></option>
        <option></option>
        <option></option>
        <option></option>
        <option></option>
      </select>
    </div>

    <!-- Pay -->
    <div>
      <input
        id="rakutenPay"
        type="checkbox"
        v-model="rakutenPay"
      >
      <label for="rakutenPay">Pay</label>
      <select v-model="rakutenPayCredit">
        <option disabled value=""></option>
        <option></option>
        <option>0.5%</option>
        <option>1%</option>
      </select>
    </div>

    <!-- auPay -->
    <div>
      <input
        id="auPay"
        type="checkbox"
        v-model="auPay"
      >
      <label for="auPay">auPay</label>
      <select v-model="auPayCredit">
        <option disabled value=""></option>
        <option></option>
        <option>0.5%</option>
        <option>1%</option>
      </select>
      <select v-model="isAu">
        <option disabled value=""></option>
        <option>au</option>
        <option>au</option>
      </select>
    </div>

    <!-- d -->
    <div>
      <input
        id="dPay"
        type="checkbox"
        v-model="dPay"
      >
      <label for="dPay">d</label>
      <select v-model="dPayCredit">
        <option disabled value=""></option>
        <option></option>
        <option>0.5%</option>
        <option>1%</option>
      </select>
    </div>

    <!--  -->
    <div>
      <input
        id="meruPay"
        type="checkbox"
        v-model="meruPay"
      >
      <label for="meruPay"></label>
    </div>

    <!-- Kyash -->
    <div>
      <input
        id="kyash"
        type="checkbox"
        v-model="kyash"
      >
      <label for="kyash">Kyash Card</label>
      <select v-model="kyash_credit">
        <option disabled value=""></option>
        <option></option>
        <option>0.5%</option>
        <option>1%</option>
      </select>
    </div>

    <button v-on:click="register"></button>
  </div>
</template>

<script>
// import Vue from 'vue'
// import axios from 'axios'
// import _json from '../assets/pay_test.json'
export default {
  data () {
    return {
      creditcard: false,
      creditcardCredit: '1%',
      paypayMoney: false,
      visaLinePay: false,
      linePay: false,
      lineRank: '',
      rakutenPay: false,
      rakutenPayCredit: '1%',
      auPay: false,
      auPayCredit: '1%',
      isAu: '',
      dPay: false,
      dPayCredit: '1%',
      meruPay: false,
      kyash: false,
      kyash_credit: '1%',
      // value
      usePayAndReturnRate: JSON.parse('{}')
    }
  },
  methods: {
    register: function () {
      // 
      this.usePayAndReturnRate = {}
      // 

      // 
      if (this.creditcard) {
        this.usePayAndReturnRate[''] = 0
        if (this.creditcardCredit === '0.5%') {
          this.usePayAndReturnRate[''] = 0.5
        }
        if (this.creditcardCredit === '1%') {
          this.usePayAndReturnRate[''] = 1
        }
      }

      // PayPay 
      if (this.paypayMoney) {
        this.usePayAndReturnRate['PayPay()'] = 0.5
      }
      // Visa Line Pay 
      if (this.visaLinePay) {
        this.usePayAndReturnRate['visa LINE Pay'] = 3

        if (this.linePay) {
          this.usePayAndReturnRate['LINE Pay'] = 1
          if (this.lineRank === '') {
            this.usePayAndReturnRate['LINE Pay'] = 1
          }
          if (this.lineRank === '') {
            this.usePayAndReturnRate['LINE Pay'] = 1.5
          }
          if (this.lineRank === '') {
            this.usePayAndReturnRate['LINE Pay'] = 2
          }
          if (this.lineRank === '') {
            this.usePayAndReturnRate['LINE Pay'] = 3
          }
        }
      } else {
        if (this.linePay) {
          this.usePayAndReturnRate['LINE Pay'] = 0
        }
      }

      // Pay
      if (this.rakutenPay) {
        this.usePayAndReturnRate['Pay'] = 0
        if (this.rakutenPayCredit === '') {
          this.usePayAndReturnRate['Pay'] = 0
        }
        if (this.rakutenPayCredit === '0.5%') {
          this.usePayAndReturnRate['Pay'] = 0.5
        }
        if (this.rakutenPayCredit === '1%') {
          this.usePayAndReturnRate['Pay'] = 1
        }
      }

      // auPay
      if (this.auPay) {
        this.usePayAndReturnRate['auPay'] = 0.5
        if (this.auPayCredit === '') {
          this.usePayAndReturnRate['auPay'] = 0.5
        }
        if (this.auPayCredit === '0.5%') {
          this.usePayAndReturnRate['auPay'] += 0.5
        }
        if (this.auPayCredit === '1%') {
          this.usePayAndReturnRate['auPay'] += 1
        }

        if (this.isAu === 'au') {
          this.usePayAndReturnRate['isAu'] = true
        } else {
          this.usePayAndReturnRate['isAu'] = false
        }
      }

      // d
      if (this.dPay) {
        this.usePayAndReturnRate['dPay'] = 0.5
        if (this.dPayCredit === '') {
          this.usePayAndReturnRate['dPay'] = 0.5
        }
        if (this.dPayCredit === '0.5%') {
          this.usePayAndReturnRate['dPay'] += 0.5
        }
        if (this.dPayCredit === '1%') {
          this.usePayAndReturnRate['dPay'] += 1
        }
      }

      // 
      if (this.meruPay) {
        this.usePayAndReturnRate['meruPay'] = 0
      }

      // kyash
      if (this.kyash) {
        this.usePayAndReturnRate['Kyash Card'] = 1

        if (this.kyash_credit === '0.5%') {
          this.usePayAndReturnRate['Kyash Card'] += 0.5
        }
        if (this.kyash_credit === '1%') {
          this.usePayAndReturnRate['Kyash Card'] += 1
        }
      }

      console.log(this.usePayAndReturnRate)
      this.$localStorage.set('usePayAndReturnRate', JSON.stringify(this.usePayAndReturnRate))
    }
  }
}
</script>

<style>
</style>



